import './ProfilePage.css';
import React, { useRef, useState, useEffect, useCallback } from 'react';
import { useParams } from "react-router-dom";
import Header from '../components/Header';
import FeedBox from '../components/FeedBox';
import userAjax from '../ajax/userAjax';

import { useRecoilState } from 'recoil';
import userInfoState from '../recoil/atoms/userInfoState';

import ProfileImg from '../components/ProfileImg';



const ProfilePage = () => {
    const inputPicRef = useRef(null);    

    const [ inputs, setInputs ] = useState({ inputPic: '' });
    const { inputPic } = inputs;

    const onChangeFile = async e => {
        if(!e.target.files[0]) { return; }
        
        const formData = new FormData();
        //formData.append('signedUserId', userInfo.userId);
        formData.append('pic', e.target.files[0]);        
        
        const { data } = await userAjax.patchUserPic(formData);        
        if(!data.resultData) { return; }
        setUserInfo(prev => {
            const prevData = { ...prev }
            prevData.pic = data.resultData;
            return prevData;
        });
    }

    const onChangeInput = e => {
        const { value, name } = e.target;
        setInputs({
            ...inputs
            , [name]: value
        });
    }


    const { userId } = useParams();    
    
    const [ profileUserId, setProfileUserId ] = useState(userId);
    
    const [ userInfo, setUserInfo ] = useRecoilState(userInfoState);    
    
    const [ profileInfo, setProfileInfo ] = useState({ favCnt: 0, feedCnt: 0, nm: '', createdAt: '', pic: '' });
    const [ followState, setFollowState ] = useState(0);

    const [ isLoading, setIsLoading ] =  useState(false);
    const [ isFinish, setIsFinish ] =  useState(true);

    const [ page, setPage ] = useState(1); 
    const [ listData, setListData ] = useState([]);
    const [ error, setError ] = useState(null);

    const handleScroll = () => {       
        if (isFinish || (parseInt(window.innerHeight + document.documentElement.scrollTop) !== document.documentElement.offsetHeight || isLoading)) {
          return;
        }
        getData();
    };
   

    const getProfileInfoData = useCallback( async () => {
        console.log('  ')
        const { data } = await userAjax.getProfileInfo(parseInt(userId));        
        if(data.resultData) {
            setProfileInfo(data.resultData);
            setFollowState(data.resultData.followState);
        }
    }, [setProfileInfo]);

    const getData = async () => {
        if(isLoading) {
            return;
        }

        setIsLoading(true);
        setError(null);

        const params = {
            page
            , profile_user_id: parseInt(userId)
        }

        console.log('getData');
        try {
            const { data } = await userAjax.getProfileFeedList(params);            

            setIsFinish(data.resultData.length < 20);

            if(data.resultData) {            
                setListData(prevData => [...prevData, ...data.resultData]);
            }            
            setPage(page + 1);
        } catch(e) {
            setError(error);
        } finally {
            setIsLoading(false);
        }
    }

    const patchUserPicSend = async e => {
        const params = {
            userId: userInfo.userId
            , pic: inputPic
        }

        const { data } = await userAjax.patchUserPic(params);        
        if(!data.result) { return; }
        setUserInfo(prev => {
            const data = { ...prev }
            data.pic = inputPic;
            return data;
        });

        const $closeBtn = document.querySelector('.btn-close');
        $closeBtn.click();

        setInputs({
            ...inputs
            , ['inputPic']: ''
        });
        
    }

    function onDelFeed(feedIdx) {
        console.log(feedIdx);
        setListData(prev => {
            prev.splice(feedIdx, 1);
            return [ ...prev ];
        });
    }

    const procFollow = async e => {
        let res = null;
        switch(followState) {        
            case 0: case 2: //post
                const postParams = {                    
                    toUserId: parseInt(userId)
                }
                res = await userAjax.postFollow(postParams);
                break;
            default: //delete
                const deleteParams = {                    
                    to_user_id: parseInt(userId)
                }
                res = await userAjax.deleteFollow(deleteParams);
            break;
        }

       
        const { data } = res;
        if(data.resultData) {            
            switch(followState) {
                case 0: case 2:
                    setFollowState(followState + 1);
                    setProfileInfo({
                        ...profileInfo
                        , follower: profileInfo.follower + 1
                    });
                    break;
                default:
                    setFollowState(followState - 1);                           
                    setProfileInfo({
                        ...profileInfo
                        , follower: profileInfo.follower - 1                    
                    });
                    break;                    
            }
        }
    }

    const getFollowStateText = state => {
        console.log(`state : ${state}`);
        switch(state) {
            case 0:
                return '';
            case 1:
            case 3:
                return ' ';
            case 2:
                return '';
        }
    }

    useEffect(() => {
        setListData([]);
        setPage(1);                
        getProfileInfoData();        
        getData();
    }, [userId]);

    useEffect(() => {
        window.addEventListener('scroll', handleScroll);
        return () => window.removeEventListener('scroll', handleScroll);
    }, [isLoading]);

    return (
        <section>
            <div className="container">
                <Header></Header>
                <div id="flexContainer">
                    <div>
                    <table>
                        <thead></thead>
                        <tbody>
                            <tr>
                                <td></td>
                                <td>{profileInfo.feedCnt}</td>
                                <td className="pl_10"></td>
                                <td>{profileInfo.favCnt}</td>
                                <td className="pointer follower pl_10"></td>
                                <td className="pointer follower">{profileInfo.follower}</td>
                                <td className="pointer follow pl_10"></td>
                                <td className="pointer follow">{profileInfo.following}</td>
                                {
                                    userInfo.userId !== parseInt(userId) &&
                                    <td className="pl_10">
                                        <input type="button" className="instaBtn" value={getFollowStateText(followState)} onClick={ procFollow } />      
                                    </td>
                                }
                            </tr>
                        </tbody>
                        <tfoot></tfoot>
                    </table>
                    <div>                           
                        {
                            parseInt(userId) === userInfo.userId 
                            ?
                            <div onClick={ () => inputPicRef.current.click() }>            
                                <ProfileImg userId={profileUserId} pic={userInfo.pic} size={300} clsValue='profile pointer' />
                            </div>
                            :
                            <ProfileImg userId={profileUserId} pic={profileInfo.pic} size={300} clsValue='profile' />
                        }                        
                    </div>
                    <table>
                        <thead></thead>
                        <tbody>                           
                            <tr>
                                <th>name</th>
                                <td>{profileInfo.nm}</td>
                            </tr>
                            <tr>
                                <th>reg date</th>
                                <td>{profileInfo.createdAt}</td>
                            </tr>
                        </tbody>
                        <tfoot></tfoot>
                    </table>
                    </div>
                </div>

                <div id="item_container">
                    { listData.map((item, idx) => <FeedBox key={item.ifeed} props={item} userId={userInfo.userId} ynDel="1" feedIdx={idx} onDelFeed={onDelFeed} />) }
                </div>
                { isLoading && <div className="loading display-none"><img src="/fimg/loading.gif" /></div> }
                { error && <p>Error: {error.message}</p> }
            </div>

            <input type="file" ref={inputPicRef} name="inputPic" onChange={onChangeFile} accept="image/*" className="visibility-hidden" />
            
        </section>
    );
}

export default ProfilePage;