import React, { useState } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation, Pagination, Scrollbar, A11y } from 'swiper/modules';
import { Link } from 'react-router-dom';
import ProfileImg from '../components/ProfileImg';
import feedAjax from '../ajax/feedAjax';
import FeedCommentBox from '../components/FeedCommentBox';

import { useRecoilState } from 'recoil';
import userInfoState from '../recoil/atoms/userInfoState';

import styles from './FeedBox.module.css';
import 'swiper/css';

const FeedBox = ({ props, feedIndex=0, onDelFeed, ynDel }) => {    
    const [ isMoreComment, setIsMoreComment ] = useState(props.isMoreComment);
    const [ commentList, setCommentList ] = useState(props.comments ? props.comments : []);
    const [ userInfo, setUserInfo ] = useRecoilState(userInfoState);    
    const [ isFav, setIsFav ] = useState(props.isFav);
    const [ inputs, setInputs ] = useState({ comment: '' });

    const { comment } = inputs;

    const onChangeInput = e => {
        const { value, name } = e.target;
        setInputs({
            ...inputs
            , [name]: value
        });
    }

    //const profilePicSrc = '/fimg/defaultProfileImg.png';

    const getDateTimeInfo = dt => {
        const nowDt = new Date();
        const targetDt = new Date(dt);
    
        const nowDtSec = parseInt(nowDt.getTime() / 1000);
        const targetDtSec = parseInt(targetDt.getTime() / 1000);
    
        const diffSec = nowDtSec - targetDtSec;
        if(diffSec < 120) {
            return '1 ';
        } else if(diffSec < 3600) { //  (60 * 60)
            return `${parseInt(diffSec / 60)} `;
        } else if(diffSec < 86400) { //  (60 * 60 * 24)
            return `${parseInt(diffSec / 3600)} `;
        } else if(diffSec < 2592000) { //  (60 * 60 * 24 * 30)
            return `${parseInt(diffSec / 86400)} `;
        }
        return targetDt.toLocaleString();
    }

    const styleItem = `${styles.Item} mb-3 p-3 w400`;
    
    const toggleFav = async () => {
        const params = {            
            'feed_id': props.feedId
        }
        const { data } = await feedAjax.toggleFeedFav(params);
        if(data) {
            setIsFav(data.resultData);         
        }
    }

    const onKeyPressComment = async e => {
        if(e.key === 'Enter') {
            await postComment();
        }
    }

    const postComment = async e => {
        if(comment.length === 0) {
            alert('  .');
            return;
        }

        const params = {
            feedId: props.feedId
            , comment
        }        

        const { data } = await feedAjax.postFeedComment(params);
        if(data.resultData) {
            setInputs({
                ...inputs
                , ['comment']: ''
            })

            if(isMoreComment) {
                getMoreComment();
                return;
            }

            const commentItem = {
                feedCommentId: data.resultData
                , writerId: userInfo.userId
                , writerNm: userInfo.nm
                , writerPic: userInfo.pic
                , comment
            }
            
            setCommentList(prev => {
                return [ ...prev, commentItem ];
            });

        }   

    }

    const getMoreComment = async e => {
        const params = {
            feed_id: props.feedId
        }

        const { data } = await feedAjax.getFeedCommentList(params);
        if(data && data.resultData.length > 0) {
            setCommentList([
                ...commentList,
                ...data.resultData
            ]);
        }
        if(isMoreComment) {
            setIsMoreComment(0);
        }
    }

    function delComment(idx) {
        setCommentList(prev => {            
            prev.splice(idx, 1);
            return [ ...prev ];
        });
    }

    const delFeed = async e => {
        if(!window.confirm('?')) { return; }
        const params = {
            feedId: props.feedId
            , userId: userInfo.userId
        }
        const { data } = await feedAjax.delFeed(params);
        if(!data.result) { console.log(' .'); return; }
        onDelFeed(feedIndex);
    }

    return (
        <div className={styleItem}>
            <div className='d-flex flex-row ps-3 pe-3'>
                <div className="d-flex flex-column justify-content-center">
                    <Link to={`/profile/${props.writerId}`}><ProfileImg userId={props.writerId} pic={props.writerPic} size={30} clsValue='pointer profile' /></Link>
                </div>
                <div className="p-3 flex-grow-1">
                    <div><Link to={`/profile/${props.writerId}`}><span className="pointer">{props.writerNm}</span></Link> - { getDateTimeInfo(props.createdAt) }</div>
                    <div>{props.location == null ? '' : props.location}</div>
                </div>
                {
                    ynDel && props.writerId && userInfo.userId &&
                    <div className="d-flex flex-column justify-content-center">
                        <i className="fa fa-trash pointer" onClick={delFeed}></i>
                    </div>
                }
            </div>
            
            <Swiper
                modules={[Navigation, Pagination, Scrollbar, A11y]}
                spaceBetween={50}
                slidesPerView={1}
                navigation
                pagination={{ clickable: true }}
                scrollbar={{ draggable: true }}>

                {
                    props.pics 
                    ? 
                    props.pics.map((item, idx) => {
                                return (
                                    <SwiperSlide key={idx}>
                                        <img src={`/pic/feed/${props.feedId}/${item}`} className="w614" />
                                    </SwiperSlide>
                                );
                            })
                    : null
                }

            </Swiper>          
            <div className="favCont p-2 d-flex flex-row">
                <i className={`${isFav ? 'fas':'far'} fa-heart pointer rem1_2 me-3`} onClick={toggleFav}></i>                
            </div>             
            {
                props.contents 
                ? <div className="itemCtnt p-2">{props.contents}</div>
                : null
            }        
            <div>
                <div>
                    {
                        commentList &&                        
                        commentList.map((item, idx) => <FeedCommentBox key={item.feedCommentId} idx={idx} props={item} onDelComment={delComment} />)                        
                    }
                    {
                        isMoreComment > 0 &&                        
                        <div>
                            <span className="pointer" onClick={getMoreComment}> </span>
                        </div>
                    }
                </div>
                <div className="p-2 d-flex flex-row div-top">
                    <input type="text" name="comment" className="flex-grow-1 my_input back_color" placeholder=" ..." value={comment} onChange={onChangeInput} onKeyUp={onKeyPressComment} />
                    <button className="btn btn-outline-primary" onClick={postComment}></button>
                </div>
            </div>
        </div>
    );
}

export default FeedBox;