import customAxios from "./customAxios";

export default {
    postSignIn: async (param) => {
        return await customAxios.post('/user/sign-in', param);
    },

    postSignUp: async (param) => {
        return await customAxios.post('/user/sign-up', param);
    },

    getProfileInfo: async (profileUserId) => {
        return await customAxios.get('/user', { params: { profile_user_id: profileUserId }});
    },

    getProfileFeedList: async (params) => {
        return await customAxios.get('/feed', { params });
    },

    patchUserPic: async params => {
        return await customAxios.patch('/user/pic', params);
    },


    postFollow: async params => {
        return await customAxios.post('/user/follow', params);
    },

    deleteFollow: async params => {
        return await customAxios.delete('/user/follow', { params });
    },
    
}