import React, { useState } from 'react';
import { useRecoilState } from 'recoil';
import userInfoState from '../recoil/atoms/userInfoState';

const ProfileImg = (props) => {            
    const [ userInfo, setUserInfo ] = useRecoilState(userInfoState);  

    const handleImgError = e => {
        console.log(e);
        e.target.src = '/fimg/defaultProfileImg.png';       
        e.target.onError = null; 
    }

    const clsValue = `circleimg ${props.clsValue} w${props.size} h${props.size}`;
    let pic = props.pic == null ?  '/fimg/defaultProfileImg.png'
                                : props.pic.startsWith('http') ? props.pic
                                                               : `/pic/user/${props.userId}/${props.pic}`;
    
    
    if(userInfo.pic && props.userId === userInfo.userId) {
        pic = userInfo.pic.startsWith('http') ? userInfo.pic : `/pic/user/${userInfo.userId}/${userInfo.pic}`;
    }
    

    return (
        <div className={clsValue}>
            <img src={pic} onError={handleImgError}/>
        </div>
    );
}

export default ProfileImg;