import React, { useRef, useState, useEffect, useCallback } from 'react';
import { useNavigate } from "react-router-dom";
import { useRecoilState } from 'recoil';
import userInfoState from '../recoil/atoms/userInfoState';

const OAuth2Handler = () => {
    const navigate = useNavigate();
    const [ userInfo, setUserInfo ] = useRecoilState(userInfoState);    
    
    useEffect(() => {

        const myUrl = new URL(window.location.href);
        const accessToken = myUrl.searchParams.get("access_token"); 
        if(accessToken === null) {
            navigate('/sign-in')
        }
        const userId = myUrl.searchParams.get("user_id"); 
        const nm = myUrl.searchParams.get("nm"); 
        const pic = myUrl.searchParams.get("pic");         
        localStorage.setItem('accessToken', accessToken);
        setUserInfo({ isLogin: true, userId, nm, pic });
        navigate('/feed');

      }, []);


      
    return (
        <div className="LoginHandeler">
            <div className="notice">
                <p> .</p>
                <p> .</p>
                <div className="spinner"></div>
            </div>
        </div>
    );
}

export default OAuth2Handler;