import React, { useState, useEffect } from 'react';
import Header from '../components/Header';
import FeedBox from '../components/FeedBox';
import feedAjax from '../ajax/feedAjax';


import { useRecoilState } from 'recoil';
import userInfoState from '../recoil/atoms/userInfoState';

const FeedPage = () => {
    const [ userInfo, setUserInfo ] = useRecoilState(userInfoState);    
    const [ inputs, setInputs ] = useState({location: '', contents: ''});
    const [ page, setPage ] = useState(1); 
    const [ listData, setListData ] = useState([]);
    const [ isLoading, setIsLoading ] = useState(false);
    const [ error, setError ] = useState(null);
    

    const [ picCnt, setPicCnt ] = useState(1);
    const { location, contents } = inputs;

    const onChangeInput = e => {
        const { value, name } = e.target;
        setInputs({
            ...inputs
            , [name]: value
        });
    }

    const handleScroll = () => {       
        if (parseInt(window.innerHeight + document.documentElement.scrollTop) !== document.documentElement.offsetHeight || isLoading) {
          return;
        }
        getData();
    };

    async function getData() {
        setIsLoading(true);
        setError(null);

        console.log('getData');
        try {
            const res = await feedAjax.getFeedList({ page });
            if(res.data) {            
                setListData(prevData => [...prevData, ...res.data.resultData]);
            }
            setPage(prev => prev + 1);                
        } catch(e) {
            setError(error);
        } finally {
            setIsLoading(false);
        }
    }

    useEffect(() => {
        getData();
    }, []);

    useEffect(() => {
        window.addEventListener('scroll', handleScroll);
        return () => window.removeEventListener('scroll', handleScroll);
    }, [isLoading]);

    const picInputRendering = () => {
        const result = [];

        for(let i=0; i<picCnt; i++) {
            const nameVal = `pic_${i}`;
            result.push(<div key={nameVal}><label>pic #{i + 1} : <input name={nameVal} type="text" onChange={onChangeInput} value={inputs[nameVal]}/></label></div>)
        }

        return result;
    }

    const addPicCnt = () => {
        setPicCnt(prev => prev + 1);        
    }

    const getCurrentTimestamp = () => {
        const today = new Date();

        const year = today.getFullYear();
        const month = ('0' + (today.getMonth() + 1)).slice(-2);
        const day = ('0' + today.getDate()).slice(-2);

        const hours = ('0' + today.getHours()).slice(-2); 
        const minutes = ('0' + today.getMinutes()).slice(-2);
        const seconds = ('0' + today.getSeconds()).slice(-2); 

        return `${year}-${month}-${day} ${hours}:${minutes}:${seconds}`;
    }

    const send = async () =>  {        

        const params = {
            userId: userInfo.userId
            , location: inputs.location
            , contents: inputs.contents
            , pics: []
        }

        for(const key in inputs) {            
            if(key.includes('pic') && inputs[key]) {
                params.pics.push(inputs[key]);
            }
        }
        const { data } = await feedAjax.postFeed(params);

        if(!(data && data.result)) {
            alert(' !');
            return;
        }

        switch(data.result) {
            case 0:
                alert(' !');
                return;
            case 2:
                alert('  .');
                return;
            case 3:
                alert('   .');
                return;                       
        }
        
        const item = {
            ...params
            , feedId: data.result
            , writerNm: userInfo.nm     
            , createdAt: getCurrentTimestamp()        
        }

        setListData(prev => [item, ...prev]);

        for(const key in inputs) { 
            inputs[key] = '';
        }
        inputs['contents'] = '';
        setPicCnt(1);

        const $closeBtn = document.querySelector('.btn-close');
        $closeBtn.click();

    }

    return (
        <div>
            <Header setListData={setListData}></Header>
            <section className="back_color">
                <div className="container d-flex flex-column align-items-center">
                    { listData.map(item => <FeedBox key={item.feedId} props={item} /> )}
                    { isLoading && <p>Loading...</p> }
                    { error && <p>Error: {error.message}</p> }
                </div>
            </section>
        </div>
    );
}

export default FeedPage;